// { defult import files }
import styled from "styled-components";
import { Colors, Size, FontFamily, FontWeight } from '../style/library';


// { common for all cases }


// alignment (TAC, TAL, TAR)
const Alignment = alignment => {
    switch(alignment){
        // center
        case "TAC":
        return "text-align:center";
        
        // left
        case "TAL":
        return "text-align:left";

        // right
        case "TAR":
        return "text-align:right";
    }
};

// floting (FL, FR, FN)

// LHS MID RHS
const LHS = styled.div`float:left`;
const MID = styled.div`float:none;`;
const RHS = styled.div`float:right`;


const FullWidthBtn = styled.a`
  text-align:center;font-size:12px;font-weight:bold;color:#13A085;border:1px solid #13A085;border-radius:4px;padding:7px 0;margin:0 16px 16px;text-transform:capitalize;
  margin:${props => props.margin};
  padding:${props => props.padding};
  color:${props => props.color};
  font-size:${props => props.fontSize};
  border:${props => props.border};
  border-radius:${props => props.borderRadius}
`;


// display (DN, DI, DB, DIB, GRD)
const DisplayType = display => {
    switch(display){
        // none
        case "DN":
        return "display:none";

        // inline
        case "DI":
        return "display:inline";
        
        // block
        case "DB":
        return "display:block"; 

        // inline block
        case "DIB":
        return "display:inline-block";
    }
}; 

// heading
const TitleType = Title =>{
    switch(Title){
        case "h6":
        return "font-size:"+Size.size20;

        case "h5":
        return "font-size:"+Size.size22;

        case "h4":
        return "font-size:"+Size.size24;

        case "h3":
        return "font-size:"+Size.size26;

        case "h2":
        return "font-size:"+Size.size28;

        case "h1":
        return "font-size:"+Size.size30;
    }
};

const SilderCong = Slider =>{

  switch(Slider){

    case "isSilder":
    return "white-space:nowrap;overflow-x:auto;"

    case "isNotSlider":
    return "white-space:normal;overflow-x:auto;"

 }
}

const Title = styled.div`
    ${({ Title }) => TitleType(Title)}
`;

 

// universal reset
function ClearFix(){
    return`
        &:after{
            content:" ";display:block;clear:both;line-height:0;height:0
        }
    `
};

// CL  
function clearBoth() {
    return `
        clear:both;
    `;
};

const UL = styled.ul``;

const Section = styled.section`
 padding:${props => props.padding};
 margin:${props => props.margin};
 `;

const LI = styled.li`
  width:${props => props.width};
  height:${props => props.height};
  padding:${props => props.padding};
  ${({ display }) => DisplayType(display)}
`;

//DetailsPagereview
const TextColor = styled.a`
&&.red{color:#DB4437;font-size:16px;font-weight:600;}
margin:${props => props.margin};
padding:${props => props.padding};
color:${props => props.color};
font-size:${props => props.fontSize};
`;
 
 
export{
    Alignment, LHS, MID, RHS, DisplayType, ClearFix, clearBoth, Title,  UL, LI, SilderCong, FullWidthBtn, Section,TextColor
}
